<template>
  <div v-if="commentList && commentList.length > 0">
    <div class="comment_mutual" v-for="(comment, index) in commentList" v-if="!comment.parentCommentE" :key="index">
      <div><img :src="comment.commentE.user ? comment.commentE.user.avatar : ''" alt="评论头像" @click="enterPerCenter(comment.commentE.user)"></div>
      <div>
        <p>{{comment.commentE.user.nickname}}</p>
        <p>{{comment.commentE.commentDate | dateFm}}</p>
        <p>
          {{comment.commentE.commentsSection}}
        </p>
      </div>
      <div v-if="account">
        <span v-if="comment.commentE.user && comment.commentE.user.userId != account" @click="replayComment(comment)">回复</span>
        <div class="delet-comment" v-if="comment.commentE.user && comment.commentE.user.userId == account" @click="deletionComment(comment,index,commentList)">删除</div>
      </div>
    </div>
    <div class="comment_mutual" v-for="(comment, index) in commentList" v-if="comment.parentCommentE" :key="index">
      <div><img :src="comment.commentE.user ? comment.commentE.user.avatar : ''" alt="评论头像" @click="enterPerCenter(comment.parentCommentE.user)"></div>
      <div>
        <p>{{comment.commentE.user.nickname}}</p>
        <p>{{comment.commentE.commentDate | dateFm}}</p>
        <div>{{comment.commentE.commentsSection}}</div>
        <!-- 评论 -->
        <div>
          <p class="replayUserName">{{comment.parentCommentE.user.nickname}}:</p>
          <p>
            {{comment.parentCommentE.commentsSection}}
          </p>
        </div>
      </div>
      <div v-if="account">
        <span v-if="comment.commentE.user && comment.commentE.user.userId != account && !comment.commentE.replyCommentId" @click="replayComment(comment)">回复</span>
        <div class="delet-comment" v-if="comment.commentE.user && comment.commentE.user.userId == account" @click="deletionComment(comment,index,commentList)">删除</div>
      </div>
    </div>
    <div class="comment_mutual_more" v-if="commentTotal > 5">
      <button @click="loadingMore">{{loadingMoreComment}}</button>
    </div>
  </div>
</template>
<script>
  export default {
    components: {
      loading,
      topic
    },
    data() {
      return {
        page: 1,
        account: '',
        commentList: new Array(),
        baseUrl: 'http://192.168.124.4:8081',
        commentTotal: 0,
        loadingMoreComment: '加载更多'
      }
    },
    created() {
      this.loadingCommentList()
    },
    methods: {
      loadingCommentList() {
        // 获取作品评论列表
        this.axios.get('/api/getWorksCommentList', {
          params: {
            worksId: this.$route.params.id,
            page: this.page,
          }
        }).then(data => {
          this.commentTotal = data.data.result.total
          if (data.data.result.list.length > 0) {
            this.loadingMoreComment = '加载更多'
            data.data.result.list.forEach(element => {
              // 评论作品者头像处理
              if (element.commentE.user && element.commentE.user.avatar.indexOf('http') == -1) {
                element.commentE.user.avatar = this.baseUrl + element.commentE.user.avatar
              }
              // 回复用户者头像
              if (element.parentCommentE && element.parentCommentE.user.avatar.indexOf('http') == -1) {
                console.log(element.parentCommentE.user.avatar)
                element.parentCommentE.user.avatar = this.baseUrl + element.parentCommentE.user.avatar
                // 回复用户者时间格式
              }
            })
            if (this.commentList.length > 0) {
              this.commentList = this.commentList.concat(data.data.result.list)
            } else {
              this.commentList = data.data.result.list
            }
          }else{
            this.$Toast({
              message: '没有更多评论了',
              position: 'middle',
              duration: 500
            })
            this.loadingMoreComment = '没有更多评论了'
          }
          this.account = data.data.account
        }).catch(err => {
          console.log(err)
        })
      },
      // 加载更多
      loadingMore() {
        this.page++
        this.showLoading = 1
        this.loadingCommentList()
        setTimeout(() => {
          this.showLoading = 0
        }, 500)
      },
      // 回复
      replayComment(workInfo) {
        console.log(workInfo)
        this.$emit('replayComment', workInfo.commentE)
      },
      // 进入个人中心
      enterPerCenter(userInfo) {
        console.log(userInfo)
        this.$router.push({
          name: 'perWorkCircle',
          params: {
            id: userInfo.userId
          }
        })
      },
      // 删除评论
      deletionComment(comment, index, commentList) {
        this.axios.get('/api/user/delectWorksCommentE', {
          params: {
            commentIds: comment.commentE.commentId
          }
        }).then(data => {
          if (data.data.status == 'success') {
            commentList.splice(index, 1)
            this.$Toast({
              message: '删除评论成功',
              position: 'middle',
              duration: 500
            })
          } else {
            this.$Toast({
              message: '删除评论失败',
              position: 'middle',
              duration: 500
            })
          }
        }).catch(err => {
          console.log(err)
        })
      },
      // 提示状态500ms
      changeTopic(info, time) {
        this.topicTitle = info
        this.showTopic = 1
        setTimeout(() => {
          this.showTopic = 0
        }, time)
      },
    }
  }
</script>


<style lang="less" scoped>
  .replayUserName {
    color: #44b336;
  }
  .comment_mutual_more {
    text-align: center;
    margin-bottom: 3.75rem;
    height: 1.875rem;
    line-height: 1.875rem;
    text-align: center;
    &>button {
      margin: 0 auto;
      background-color: #fff;
      border: none;
      border: 0;
      outline: none;
      color: #000;
      display: block;
      width: 9.375rem;
      height: 2.5rem;
      line-height: 2.5rem;
      border-radius: 1.875rem;
    }
  }
  .comment_mutual {
    &:nth-of-type(1) {
      padding-top: 1.25rem;
    }
    &:last-child {
      margin-bottom: 3.125rem;
    }
    display: flex;
    background-color: #fff;
    padding-top: .625rem;
    &>div:nth-child(1) {
      flex: 2;
      text-align: center;
      &>img {
        width: 3.125rem;
        height: 3.125rem;
        border-radius: 50%;
      }
    }
    &>div:nth-child(2) {
      flex: 6;
      border-bottom: .0625rem solid rgba(0, 0, 0, .3);
      &>p:nth-child(1) {
        color: #44b336;
      }
      &>p:nth-child(2) {
        color: #999;
        font-size: .875rem;
        padding: .3125rem 0;
      }
      &>p:nth-child(3) {
        padding-top: 1%;
      }
    }
    &>div:nth-child(3) {
      flex: 2;
      padding-top: 2%;
      border-bottom: .0625rem solid rgba(0, 0, 0, 0.3);
      color: rgba(0, 0, 0, .6);
    }
  }
</style>



